var Vue = require("vue/dist/vue.min.js");
var Router = require("../router.js");

var Home = {
    beforeMount: function() {
        // this.$emit('getUserInfo');
        if (this.userInfo == null || this.userInfo.cert_user_id == null) {
            Router.navigate('');
            console.log("Redirected to LP", this.userInfo);
        } else {
            if (Router.currentParams.hasOwnProperty("c") && Router.currentParams.c != "") {
                this.$emit("setConversation", Router.currentParams.c);
            } else {
                this.$emit("setConversation", "");
            }

            console.log("Staying on Home", Router.currentParams);
        }

        console.log("Loaded App (home)", this.userInfo, this.conversation, this.conversationInfo);
    },
    props: {
        userInfo: Object,
        siteInfo: Object,
        conversation: String,
        conversationInfo: Object,
        messages: Array,
        theme: String
    },
    data: function() {
        return {
            msgInput: "abc123",
            theme: this.theme,
            messages: this.messages
        }
    },
    computed: {
        isLoggedIn: function() {
            if (this.userInfo == null) return false;
            return this.userInfo.cert_user_id != null;
        }
    },
    watch: {
        conversation: function() {
            // console.log("Watching conversation", this.conversation, this.conversationInfo);

            this.messages = this.messages;

            return this.conversation;
        },
        theme: function() {
            // console.log("Watching theme", this.theme);

            return this.theme;
        }
    },
    methods: {
        toggleLeftSidebar: function() {
            this.$emit('toggleLeftSidebar');
        },
        logIn: function() {
            this.$emit('logIn');
        },
        logOut: function() {
            this.$emit('logOut');
        },
        goto: function(to) {
            Router.navigate(to);
        },
        setConversation: function(to) {
            this.$emit("setConversation", to);
        },
        sendMessage: function() {
            // var dis = this;
            // this.$emit("sendMessage", this.msgInput, function() {
            //     setTimeout(function() {
            //         console.log("CB");
            //         dis.$forceUpdate();
            //     }, 1000);
            // });


            if (this.msgInput && /\S/.test(this.msgInput)) {
                var dis = this;
                this.$emit("sendMessage", this.msgInput, function(newMSG) {
                    dis.$forceUpdate();

                    const data_inner_path = "data/users/" + dis.siteInfo.auth_address + "/data.json";
                    const data2_inner_path = "data/users/" + dis.siteInfo.auth_address + "/data_private.json";
                    const content_inner_path = "data/users/" + dis.siteInfo.auth_address + "/content.json";

                    page.execTW("fileGet", {
                        "inner_path": data_inner_path,
                        "required": false
                    }, (data) => {
                        if (data) {
                            var data = JSON.parse(data);
                        } else {
                            var data = {};
                        }

                        if (!data.hasOwnProperty("messages")) {
                            data.messages = [];
                        }
                        if (!data.hasOwnProperty("private_messages")) {
                            data.private_messages = [];
                        }
                        if (!data.hasOwnProperty("group_messages")) {
                            data.group_messages = [];
                        }
                        if (!data.hasOwnProperty("images")) {
                            data.images = [];
                        }

                        var addMSG = function(to) {
                            if (to === "messages") {
                                data[to].push({
                                    "body": newMSG.body,
                                    "date_added": newMSG.date_added,
                                    "key": newMSG.key
                                });

                                page.writeBlaTo(0, data);
                            } else if (to === "private_messages" || to === "group_messages") {
                                data[to].push({
                                    "message": newMSG.res
                                });

                                page.writeBlaTo(0, data);
                            }

                            console.log("Data after addMSG", JSON.parse(JSON.stringify(data)));
                        };

                        var makeMSG = {
                            "Lobby": function() {
                                addMSG("messages");
                            },
                            "private": function() {
                                var recipient = dis.conversation;
                                page.cmd("dbQuery", [
                                    // "SELECT * FROM keyvalue LEFT JOIN json USING (json_id) WHERE key = 'public_key' AND value NOT NULL AND json.cert_user_id = '" + recipient + "'"
                                    "SELECT * FROM extra_data LEFT JOIN json USING (json_id) WHERE json.auth_address = '" + recipient + "'"
                                ], (ousers) => {
                                    var ruser = ousers[0];
                                    console.log(ruser);
                                    if (ruser && ruser.hasOwnProperty("public_key") && ruser.public_key) {
                                        page.cmd("eciesEncrypt", [
                                            JSON.stringify({
                                                "recipient": recipient,
                                                "body": newMSG.body,
                                                "date_added": newMSG.date_added
                                            }),
                                            ruser.public_key
                                        ], (res) => {
                                            newMSG.res = res;

                                            addMSG("private_messages");
                                        });
                                    }
                                });
                            },
                            "group": function() {
                                var group = dis.conversation || false;
                                if (!group) {
                                    group = Math.random().toString(36).substring(2).toString();
                                }

                                var rgroup = group;
                                if (group.length < 16) {
                                    var addStr = Math.seed(group.charCodeAt(0))().toString().substring(2) + Math.seed(group.charCodeAt(1))().toString().substring(2);
                                    group += addStr;
                                    // console.log("AddStr: ", addStr, group);
                                }

                                var bR = btoa(group.substr(group.length / 2 % 7, group.length / 3 % 5) + group.substr((group.length * group.length % 3) + 1) + group.substr(group.length - 3, 2) + group);

                                page.cmd("aesEncrypt", [
                                    JSON.stringify({
                                        "group": group,
                                        "body": newMSG.body,
                                        "date_added": newMSG.date_added
                                    }),
                                    bR,
                                    bR
                                ], (res) => {
                                    newMSG.res = res[2];

                                    addMSG("group_messages");
                                });
                            },
                            "p2p": function() {
                                // add p2p-stuff
                            }
                        };

                        makeMSG[dis.conversationInfo.type]();
                    });
                });

                this.msgInput = "";
            }
        },
        sendMessage2: function(msg) {
            // var dis = this;
            // this.$emit("sendMessage", msg, function() {
            //     setTimeout(function() {
            //         console.log("CB");
            //         dis.$forceUpdate();
            //     }, 1000);
            // });

            this.$emit("sendMessage", msg);

            this.$forceUpdate();
        }
    },
    template: `
        <div>
            <div v-if="isLoggedIn">
                <div v-if="messages" class="panel messages_renderer">
                    <div class="messagesAt label fixed"></div>

                    <div class="panel-body" style="overflow-x: hidden;">
                        <button id="scrolltobottombtn" class="btn btn-action btn-lg btn-primary shadow-drop-2-bottom circle hide"><i class="icon icon-arrow-down"></i></button>

                        <div v-if="messages && !messages.length > 0" class="empty m-2">
                            <div class="empty-icon">
                                <i class="icon icon-people"></i>
                            </div>
                            <p class="empty-title h4">Be the first in <i>{{ conversationInfo.name }}</i></p>
                            <p class="empty-subtitle">Start the discussion by sending a message!</p>
                            <div class="empty-action">
                                <button class="btn btn-primary" v-on:click.prevent="sendMessage2('Hello! :)');">Send "Hello! :)"</button>
                            </div>
                        </div>
                        <messages v-else
                        v-bind:userInfo="userInfo" v-bind:siteInfo="siteInfo"
                        v-bind:messages="messages"
                        v-bind:theme="theme"></messages>
                    </div>
                    <div class="panel-footer bg-gray">
                        <div class="input-group">
                            <button class="btn btn-primary btn-lg btn-action input-group-btn media_selector_btn" id="media_selector_btn"><i class="icon icon-photo"></i></button>
                            <button class="btn btn-lg btn-action input-group-btn hide media_selector_cancel_btn" id="media_selector_cancel_btn"><i class="icon icon-cross"></i></button>
                            <!--<span class="input-group-addon addon-lg"><a href="#Select+user" id="select_user" v-on:click.prevent="selectUser">Select user</a></span>-->
                            <!--<input type="text" class="form-input input-lg" placeholder="Message.." id="message" v-on:keypress.enter.exact.prevent="sendMessage" />-->
                            <textarea v-model="msgInput" class="form-input input-lg message" placeholder="Message.." id="message" v-on:keypress.enter.exact.prevent="sendMessage" rows="1"></textarea>
                            <button class="btn btn-primary btn-lg btn-action input-group-btn" id="send" v-on:click.prevent="sendMessage"><i class="icon icon-forward"></i></button>
                        </div>
                    </div>
                </div>
                <div v-else class="empty m-2">
                    <div class="empty-icon">
                        <i class="icon icon-people"></i>
                    </div>
                    <p class="empty-title h5">Start a conversation</p>
                    <p class="empty-subtitle">Open the sidebar and choose one or join the lobby!</p>
                    <div class="empty-action">
                        <button class="btn btn-primary" v-on:click.prevent="setConversation('lobby')">Join lobby</button>
                    </div>
                    <div class="empty-action">
                        <button class="btn btn-link" v-on:click.prevent="toggleLeftSidebar">Open sidebar</button>
                    </div>
                </div>
            </div>
            <div v-else class="empty m-2">
                <div class="empty-icon">
                    <i class="icon icon-people"></i>
                </div>
                <p class="empty-title h3">ThunderWave</p>
                <p class="empty-subtitle">Please log in to use ThunderWave!</p>
                <div class="empty-action">
                    <button class="btn btn-primary" v-on:click.prevent="logIn">Log in</button>
                </div>
            </div>
        </div>
    `
};

module.exports = Home;