var Vue = require("vue/dist/vue.min.js");
var Router = require("../router.js");

Vue.component("conversation-info", {
    props: ["active", "conversation", "conversationInfo"],
    data: function() {
        return {
            active: this.active,
            conversation: this.conversation,
            conversationInfo: this.conversationInfo
        }
    },
    computed: {
        isLoggedIn: function() {
            if (this.userInfo == null) return false;
            return this.userInfo.cert_user_id != null;
        }
    },
    methods: {
        toggleConversationInfoActive: function(to) {
            this.$emit('toggleConversationInfoActive', to);
        },
        goto: function(to) {
            this.menuShown = false;
            Router.navigate(to);
        }
    },
    template: `
    <div v-bind:class="'modal ' + (active ? 'active' : '')" v-bind:id="'conversationInfo' + conversation">
        <a href="#close" v-on:click.prevent="toggleConversationInfoActive(false)" class="modal-overlay" aria-label="Close"></a>
        <div class="modal-container">
            <div class="modal-header">
                <a href="#close" v-on:click.prevent="toggleConversationInfoActive(false)" class="btn btn-clear float-right" aria-label="Close"></a>
                <div class="modal-title h5">{{ conversationInfo.name }}</div>
            </div>
            <div class="modal-body">
                <div class="content">
                    <p>
                        Conversation's ID: {{ conversation }}<br />
                        Conversation's Name: {{ conversationInfo.name }}<br />
                        Conversation's Type: {{ conversationInfo.type }}
                    </p>
                </div>
            </div>
        </div>
    </div>
    `
});