var Vue = require("vue/dist/vue.min.js");
var Router = require("../router.js");

Vue.component("messages", {
    props: {
        userInfo: Object,
        siteInfo: Object,
        messages: Array,
        theme: String
    },
    data: function() {
        return {
            messages: this.messages,
            theme: this.theme
        }
    },
    computed: {
        rcurdate: function() {
            return moment().format("MMMM Do, YYYY");
        }
    },
    watch: {
        messages: function() {
            console.log("Watching messages", this.messages);

            return this.messages;
        },
        theme: function() {
            // console.log("Watching theme", this.theme);

            return this.theme;
        }
    },
    methods: {
        goto: function(to) {
            Router.navigate(to);
        },
        usersOwnMsg: function(cert_user_id) {
            return cert_user_id === this.userInfo.cert_user_id;
        },
        curdate3: function(ts) {
            return (
                moment(ts, "x").format("MMMM Do, YYYY") === this.rcurdate ?
                "Today" :
                (
                    moment(this.rcurdate, "MMMM Do, YYYY").subtract(1, "d").format("MMMM Do, YYYY") === moment(ts, "x").format("MMMM Do, YYYY") ?
                    "Yesterday" :
                    moment(ts, "x").format("MMMM Do, YYYY")
                )
            );
        }
    },
    template: `
        <ul class="unstyled message-list" v-bind:design-type="'2'">

            <li v-for="(day_msgs, index1) in messages"
                class="day"
                >` +
        // `{{ (moment(day_msgs[0], "x").format("MMMM Do, YYYY") === rcurdate ? "Today" : (moment(rcurdate, "MMMM Do, YYYY").subtract(1, "d").format("MMMM Do, YYYY") === moment(day_msgs[0], "x").format("MMMM Do, YYYY") ? "Yesterday" : moment(day_msgs[0], "x").format("MMMM Do, YYYY"))) }}` +
        // `<div class='divider text-center' data-content='" + (curdate3) + "' onclick="return false; window.location.hash='curdate2"; return false;"></div>`+
        `
                <div class="divider text-center" v-bind:data-content="curdate3(day_msgs[0])"></div>
                <ul class="day-list unstyled">
                    <li v-for="(min_msgs, index2) in day_msgs[1]"
                        v-bind:id="'t_' + min_msgs[2][0].key" class="message-container"
                        v-bind:message-owner="min_msgs[0]" v-bind:users-own-message="usersOwnMsg(min_msgs[0])"
                        >` +
        // `{{ min_msgs[0] }} {{ moment(min_msgs[1], "x").format("HH:mm:ss") }}` +
        `
                        <div v-bind:class="'tile ' + theme">
                            <message-avatar v-if="!usersOwnMsg(min_msgs[0])" v-bind:cert_user_id="min_msgs[0]"></message-avatar>
                            <div class="tile-content">
                                <message v-for="(msg, index3) in min_msgs[2]"
                                v-bind:index="index3" v-bind:lmsg="index3 > 0 ? min_msgs[2][index3 - 1] : null"
                                v-bind:certUserId="userInfo.cert_user_id"
                                v-bind:message="msg" v-bind:theme="theme"></message>
                            </div>
                            <message-avatar v-if="usersOwnMsg(min_msgs[0])" v-bind:cert_user_id="min_msgs[0]"></message-avatar>
                        </div>
                    </li>
                </ul>
            </li>
        </ul>
    `
});