var Vue = require("vue/dist/vue.min.js");
var Router = require("../router.js");

Vue.component("app-sidebar", {
    props: ["userInfo", "siteInfo", "leftSidebarShown", "pMessages", "conversation", "conversations", "conversationsInfos", "theme"],
    data: function() {
        return {
            conversation: this.conversation,
            conversations: this.conversations,
            conversationsInfos: this.conversationsInfos,
            leftSidebarShown: this.leftSidebarShown
        }
    },
    computed: {
        isLoggedIn: function() {
            if (this.userInfo == null) return false;
            return this.userInfo.cert_user_id != null;
        }
    },
    methods: {
        toggleLeftSidebar: function() {
            this.$emit('toggleLeftSidebar');
        },
        logIn: function() {
            this.$emit('logIn');
        },
        logOut: function() {
            this.$emit('logOut');
        },
        goto: function(to) {
            this.menuShown = false;
            Router.navigate(to);
        },
        setConversation: function(to) {
            this.$emit("setConversation", to);
        },
        getLMsg: function(recipient) {
            let chat = this.pMessages(recipient);

            // console.log(chat);

            let l_day = chat[chat.length - 1];

            // console.log(l_day);

            let l_stamp = l_day[1][l_day[1].length - 1];

            // console.log(l_stamp);

            let l_user = l_stamp[0];
            let l_msgs = l_stamp[2];

            // console.log(l_user, l_msgs);

            let l_msg = l_msgs[l_msgs.length - 1];

            // console.log(l_msg);

            return l_msg;
        }
    },
    template: `
        <div id="sidebar-left"
        v-bind:class="'app-sidebar off-canvas-sidebar ' + (leftSidebarShown ? 'active' : '')">
            <div class="app-nav app-nav-panel">
                <div class="panel">
                    <div class="panel-header">
                        <div class="app-brand text-center">
                            <a href="/?/" v-on:click.prevent="setConversation('')" class="app-logo">
                                <img src="" />
                                <h2>ThunderWave</h2>
                            </a>
                            <!--<route-link to="" class="app-logo">
                            <img src="" />
                            <h2>Thunder<img src="/favicon.png" />Wave</h2>
                        </route-link>-->
                        </div>
                        <div v-if="isLoggedIn" class="input-group">
                            <input type="search" class="form-input" placeholder="Search">
                            <button class="btn btn-action input-group-btn"><i class="mdi">search</i></button>
                        </div>
                        <button v-if="!isLoggedIn" class="btn centered mt-2" v-on:click.prevent="logIn">Log in</button>
                    </div>
                    <div v-if="isLoggedIn" class="panel-body">
                        <ul class="menu">
                            <li class="menu-item">
                                <chat-preview recipient="lobby" v-bind:conversation="conversation" v-bind:conversationInfo="conversationsInfos['lobby']" v-on:setConversation="setConversation" v-bind:message="conversations.hasOwnProperty('lobby') ? getLMsg('lobby') : { body: '' }"></chat-preview>
                            </li>
                            <li class="menu-item" v-for="(i) in 1">
                                <chat-preview v-bind:recipient="'chat' + i" v-bind:conversation="conversation" v-bind:conversationInfo="conversationsInfos['chat' + i]" v-on:setConversation="setConversation" v-bind:message="conversations.hasOwnProperty('chat' + i) ? getLMsg('chat' + i) : { body: '' }"></chat-preview>
                            </li>
                        </ul>
                    </div>
                    <div v-else="!isLoggedIn" class="panel-body">
                    
                    </div>
                    <div class="panel-footer">
                        <ul class="menu">
                            <!--<avatar v-if="isLoggedIn" v-bind:cert_user_id="userInfo.cert_user_id"></avatar>
                            <avatar v-else cert_user_id=""></avatar>
                            <li class="divider"></li>-->
                            <!--<li class="menu-item">
                                <a href="./?/app/lobby" v-on:click.prevent="goto('/app/lobby');">
                                    <i class="mdi">public</i> Lobby
                                </a>
                            </li>
                            <li class="divider"></li>-->
                            <li class="menu-item">
                                <route-link to="app/settings"><i class="mdi">settings</i> Settings</route-link>
                            </li>
                            <li class="divider" data-content="LINKS"></li>
                            <li class="menu-item">
                                <a href="https://github.com/AnthyG/ThunderWave" v-on:click.prevent="return openNewTab('https://github.com/AnthyG/ThunderWave');">
                                    <i class="icon icon-link"></i> GitHub
                                </a>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    `
});