var Vue = require("vue/dist/vue.min.js");
var Router = require("../router.js");

Vue.component("message-avatar", {
    props: ["cert_user_id"],
    data: function() {
        return {
            cert_user_id: this.cert_user_id
        }
    },
    template: `
        <div class="tile-icon">
            <avatar-img v-bind:cert_user_id="cert_user_id" size="lg" class="message-user-avatar"></avatar-img>
        </div>
    `
})

Vue.component("message", {
    props: ["certUserId", "message", "theme"],
    data: function() {
        return {
            index: this.index,
            lmsg: this.lmsg,
            cert_user_id: this.certUserId,
            message: this.message
        }
    },
    computed: {
        mmnt: function() {
            return moment(this.message.date_added, "x");
        },
        curdate: function() {
            return this.mmnt.format("MMMM Do, YYYY");
        },
        curtime: function() {
            return this.mmnt.format("HH:mm:ss");
        },
        usersOwnMsg: function() {
            // console.log(this.message.cert_user_id, this.cert_user_id, this.message.cert_user_id === this.cert_user_id);
            return this.message.cert_user_id === this.cert_user_id;
        },
        body: function() {
            /**
                Time-Translator:
                ([0-9]{1,2}(?:(([aA]|[pP])[mM]|)))(:([0-9]{1,2}(?:(([aA]|[pP])[mM]|))))?

                0am
                0pm
                0Am
                0Pm
                0AM
                0PM
                00am
                00pm
                0:0am
                0:0pm
                0:00am
                0:00pm
                00:00
                00:00am
                00:00pm
                000000000000
            */

            return marked(this.message.body, {
                    renderer: markedR
                })
                .replace(/((?:(?:[\w]+)@(?:zeroid|zeroverse|kaffie|cryptoid)\.bit)|@(?:[\w]+))/gmi, function(match, p1) { // ((?:[\w]+)@(?:zeroid|zeroverse)\.bit)
                    var profile_link_part = (page.LS.opts.parse_profile_links.value ? '<a class="message-profile-link" onclick="add2MSGInput(\'' + p1 + ' \', 2); return false;" href="?u/' + encodeURI(p1) + '">' + p1 + '</a>' : '<span class="message-profile-link">' + p1 + '</span>');
                    var isthisuser = (p1.match(new RegExp(page.site_info.cert_user_id + "|@" + page.site_info.cert_user_id.split("@")[0], "gmi"))) ? true : false;
                    return (isthisuser ? "<mark>" : "") + profile_link_part + (isthisuser ? "</mark>" : "");
                })
                .replace(/(?:\?\!\[tc_((.*?)(.{8}-.{4}-.{4}-.{4}-.{12}))\])/gm, function(match, p1) {
                    if (page.LS.opts.parse_quotes.value) {
                        // page.quoteDisplayer(p1);
                    }

                    return '<div id="QUOTEREPLACE_' + p1 + '" class="' + (page.LS.opts.parse_quotes.value ? 'icon icons loading' : '') + '"><cite><q>?![tc_' + p1 + '</q> (Quote)</cite></div>';
                });
        }
    },
    watch: {
        message: function() {
            // console.log("Watching message", this.message);
            return this.message;
        }
    },
    methods: {
        goto: function(to) {
            Router.navigate(to);
        }
    },
    template: `
        <div v-bind:id="'tc_' + message.key" v-bind:class="'card mb-5 ' + theme + (index > 0 ? ' follow-up ' : ' ')"
        v-bind:user_cert_id="cert_user_id" v-bind:usersownmsg="usersOwnMsg ? 'true' : 'false'"
        v-bind:theme="theme"
        v-bind:index="index"
        >
            <div v-if="!usersOwnMsg && (lmsg ? lmsg.cert_user_id !== message.cert_user_id : true)" class="card-header"><small class="tile-title"><a href="#">{{ message.cert_user_id }}</a></small></div>
            <div class="card-body text-break" v-html="body"></div>
            <div class="">
                <small class="tile-subtitle float-right">
                    <a class="message-timestamp" href="#">{{ curtime }}</a>
                </small>
            </div>
        </div>
    `
});