var Vue = require("vue/dist/vue.min.js");
var Router = require("../router.js");

Vue.component("chat-preview", {
    props: ["recipient", "message", "conversation", "conversationInfo"],
    data: function() {
        return {
            message: this.message,
            conversation: this.conversation,
            conversationInfo: this.conversationInfo
        }
    },
    computed: {},
    watch: {
        message: function() {
            return this.message;
        },
        conversation: function() {
            return this.conversation;
        },
        conversationInfo: function() {
            return this.conversationInfo;
        }
    },
    methods: {
        goto: function(to) {
            Router.navigate(to);
        },
        setConversation: function(to) {
            console.log("Setting conversation", this.recipient);

            this.$emit("setConversation", this.recipient);
        },
        mmnt: function() {
            return moment(this.message.date_added, "x");
        },
        curdate: function() {
            return this.mmnt.format("MMMM Do, YYYY");
        },
        curtime: function() {
            return this.mmnt.format("HH:mm:ss");
        },
        curhuman: function() {
            // console.log("curhuman");
            return moment(this.message.date_added, "x").fromNow();
        }
    },
    // template: `
    //     <route-link v-bind:to="'app/c/' + recipient" v-on:click="setConversation('/app/c/' + recipient)">
    //         <div class="tile tile-centered">
    //             <div class="tile-icon">
    //                 <i class="mdi">public</i>
    //             </div>
    //             <div class="tile-content">
    //                 <div class="tile-title">
    //                     {{ recipient }} <small class="float-right text-gray">Timestamp</small>
    //                 </div>
    //                 <div class="tile-subtitle text-gray">{{ message.body }}</div>
    //             </div>` +
    //     // <div class="tile-action">
    //     //     <button class="btn btn-link">
    //     //     <i class="icon icon-more-vert"></i>
    //     //     </button>
    //     // </div>
    //     `
    //         </div>
    //     </route-link>
    // `,

    template: `
        <a v-bind:class="(recipient === conversation ? 'active' : '')" v-bind:href="'/?/app/c/' + recipient" v-on:click.prevent="setConversation('/app/c/' + recipient)">
            <div class="tile tile-centered">
                <div class="tile-icon">
                    <i class="mdi">public</i>
                </div>
                <div class="tile-content">
                    <div class="tile-title">
                        {{ conversationInfo.name }} <small class="float-right text-gray"><span v-if="message.date_added">{{ curhuman() }}</span></small>
                    </div>
                    <div class="tile-subtitle text-gray">{{ message.body }}</div>
                </div>` +
        // <div class="tile-action">
        //     <button class="btn btn-link">
        //     <i class="icon icon-more-vert"></i>
        //     </button>
        // </div>
        `
            </div>
        </a>
    `
});