var Vue = require("vue/dist/vue.min.js");
var Router = require("../router.js");

Vue.component("navbar", {
    props: ["userInfo", "siteInfo", "conversation", "conversationInfo"],
    data: function() {
        return {
            menuShown: false,
            conversation: this.conversation,
            conversationInfo: this.conversationInfo,
            conversationInfoActive: false
        }
    },
    computed: {
        isLoggedIn: function() {
            if (this.userInfo == null) return false;
            return this.userInfo.cert_user_id != null;
        }
    },
    methods: {
        toggleMenu: function(to) {
            if (typeof to === "boolean")
                this.menuShown = to;
            else
                this.menuShown = !this.menuShown;
        },
        toggleLeftSidebar: function() {
            this.$emit('toggleLeftSidebar');
        },
        logIn: function() {
            this.$emit('logIn');
        },
        logOut: function() {
            this.$emit('logOut');
        },
        goto: function(to) {
            this.menuShown = false;
            Router.navigate(to);
        },
        toggleConversationInfoActive: function(to) {
            if (to && typeof to === "boolean") {
                this.conversationInfoActive = to;
            } else {
                this.conversationInfoActive = !this.conversationInfoActive;
            }
        }
    },
    template: `
        <header id="navbar" class="navbar bg-gray">
            <nav class="navbar container #grid-lg">
                <section class="navbar-section">
                    <a class="off-canvas-toggle btn btn-link btn-action" href="#sidebar-left" v-on:click.prevent="toggleLeftSidebar">
                        <i class="icon icon-menu"></i>
                    </a>
                </section>
                <section class="navbar-center">
                    <span v-if="conversationInfo ? true : false"><a v-bind:href="'#'" v-on:click.prevent="toggleConversationInfoActive(true)">{{ conversationInfo.name }}</a></span>
                    <span v-else>ThunderWave</span>
                </section>
                <section class="navbar-section">
                    <div class="dropdown dropdown-right" v-bind:class="{'active': menuShown}">
                        <button class="btn btn-action btn-lg btn-link circle dropdown-btn" tabindex="0" v-on:focus="toggleMenu(true)" v-on:mousedown="toggleMenu" v-on:blur="toggleMenu(false)">
                            <div style="margin-top: -.3rem;">
                                <avatar-img v-if="isLoggedIn" v-bind:cert_user_id="userInfo.cert_user_id"></avatar-img>
                                <avatar-img v-else cert_user_id=""></avatar-img>
                            </div>
                        </button>
                        <ul class="menu light">
                            <li class="menu-item">
                                <avatar v-if="isLoggedIn" v-bind:cert_user_id="userInfo.cert_user_id" size="xl"></avatar>
                                <avatar v-else cert_user_id="" size="xl"></avatar>
                            </li>
                            <li class="divider"></li>
                            <li class="menu-item">
                                <a v-if="isLoggedIn" href="#Select+user" id="select_user" v-on:click.prevent="logOut">
                                    Log out
                                </a>
                                <a v-else href="#Select+user" id="select_user" v-on:click.prevent="logIn">
                                    Log in
                                </a>
                            </li>
                        </ul>
                    </div>
                </section>
            </nav>
            <conversation-info v-if="conversationInfo"
            v-bind:active="conversationInfoActive"
            v-on:toggleConversationInfoActive="toggleConversationInfoActive"
            v-bind:conversation="conversation" v-bind:conversationInfo="conversationInfo"
            ></conversation-info>
        </header>
    `
});